import React from 'react';

import Heading from './Display.jsx';

const flexboxCenter = {
	display: 'flex',
	alignItems: 'center',
	justifyContent: 'center',
};

const iconStyle = {
	height: '',
};

const Header = () => {
	let style = {
		width: '100%',
		height: '12.5%',
		backgroundColor: '#F7ECD2',
		top: '0%',
	};
	style = Object.assign(style, flexboxCenter);
	return (
		<div id="header" style={style}>
			<BookInfo />
			<AbrahamLogo />
			<ViewSettings />
		</div>
	);
};

const AbrahamLogo = () => {
	const style = {
		height: '72%',
	};
	return (<img style={style} src="assets/images/logo.svg" alt="Abraham" />);
};

const BookInfo = () => {
	return (<div id="bookInfo" style={Object.assign({
		flexDirection: 'column',
		height: '70%',
	}, flexboxCenter)}>
		<img src="assets/images/book_icon.svg" alt="Pick a book" />
		<Heading type={1}>Genesis 1</Heading>
	</div>);
};

const ViewSettings = () =>
	<div id="viewSettings" style={Object.assign({ flexDirection: 'column' }, flexboxCenter)}>
		<img src="assets/images/gear_icon.svg" alt="Settings" />
		<Heading type={1}>Classic View</Heading>
	</div>;

export default Header;



